* {
    margin: 0;
    padding: 0;
}

.chart-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
}

.chart-container::before {
    content: '';
    background: url('../img/background.png') 
        no-repeat center center/cover;
    position: absolute;
    opacity: 2;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    z-index: -1;
}

.change-button {
    background-color: #0c7778;
    color: white;
    padding: 10px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}