.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

* {
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #a0c1d954;
}

.img {
  width: 35vw;
  height: 94vh;
  position: relative;

  img {
    width: 100%;
    height: 100%;
  }

  &__text {
    position: absolute;
    z-index: 999;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 85%;
    text-align: center;
  }
}

.form {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35vw;
  height: 94vh;
  background-color: white;

  &__heading {
    margin: 10px 0px;
  }

  &__input input[type=text],
  &__input input[type=password] {
    width: 100%;
    padding: 10px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 15px;
  }

  &__input label {
    color: #245f7c;
  }

  &__check {
    padding: 12px 0px;
    display: flex;
    align-items: center;

    label {
      font-weight: bold;
      margin-left: 5px;
    }

    input {
      width: 20px;
      height: 17px;
    }

    :hover {
      cursor: pointer;
    }
  }

  &__input--blue {
    width: 40%;
    background-color: #0c7778;
    color: white;
    padding: 10px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }

  &__error {
    color: red;
    font-size: 14px;
    margin-top: -27px;
    margin-bottom: -19px;
  }

  div {
    margin: 18px 0px;
  }
}
